<script>

import { mapActions,mapState, mapMutations } from 'vuex';
import axios from 'axios';

export default {
   name: 'HomeCenter',
   props:['data'],
   data() {
       return {
           arr:[
                { title:'我的猪猪' , img:require('../../static/images/background/mine_pig.png') , path:'/minePig'},
                { title:'我的团队' , img:require('../../static/images/background/team.png') , path:'/team'},
                { title:'超级玛特' , img:require('../../static/images/background/SuperMT.png') , path:''},
                { title:'钱包' , img:require('../../static/images/background/wallet2.png') , path:'/wallet'},
                { title:'推荐好友' , img:require('../../static/images/background/Friend.png') , path:''}, // /friend
                { title:'在线客服' , img:require('../../static/images/background/Service.png') , path:''},
                { title:'区块链浏览器' , img:require('../../static/images/background/Browser.png') , path:''}, // /browser
                { title:'推荐好友' , img:require('../../static/images/background/Friend.png'), path:''},  // /friend
                { title:'每日活动' , img:require('../../static/images/background/Activity.png') , path:''},    
           ],
           userInfo:{
               nickName:'',
               amount:'',
               integral:'',
               level:'',
               avatar:'',
               token_set:''
           },
           news:[
               { title:'我是1新闻' },
               { title:'我是2新闻' },
               { title:'我是3新闻' },
               { title:'我是4新闻' },
               { title:'我是5新闻' },
               { title:'我是6新闻' },
               { title:'我是7新闻' },
               { title:'我是8新闻' },
               { title:'我是9新闻' },
               { title:'我是10新闻' },
           ],
           showIndex: 0,
       }
   },
   created(){
        this.userinfo();
   },
   methods:{
       change(index){
            this.showIndex = index;     
            if( this.arr[index].path == '' ){
                this.$dialog.alert({
                    message: '该功能暂未开放，敬请期待！'
                });
            }else{
                let p = this.arr[index].path;
                this.$router.push({path:p});
            }
        },
        userinfo(){
            let url = 'userLogin/getUserInfo';
            this.$http.post(url,{
                cmd:'1',
                body: {
                    accessToken: localStorage.token,
                    did: '1'
                }
            }).then( (res)=>{
                // console.log(res);
                localStorage.appid = res.appid;
                localStorage.uid = res.uid;
                this.userInfo.avatar = localStorage.avatar = res.avatar;
                this.userInfo.nickName = localStorage.nickName = res.nickname;
                this.userInfo.level = localStorage.level = res.level;
                this.userInfo.integral = localStorage.integral = res.integral;
                res.token_set.forEach( (item) => {
                    if( item.is_default === 1 ){
                        this.userInfo.token_set = item;
                        localStorage.token_set = JSON.stringify(item);
                    }
                })
            })
        },
   }
}
</script>

