<template>
   <div class="home_bot">
        <div class='home_but' v-for='(item,index) in arr2' 
        :key="index"
        @click='change(index)'>
            <span>
                <img :src="index===showIndex ? item.Tab : item.img" alt="">
            </span>
            <p :class='{current:index === showIndex}'>
                {{item.title}}
            </p>
        </div>
   </div>
</template>

<script>
export default {
   name: 'Tab',
   props:['Index'],
   data() {
       return {
           arr2:[
                { title:'首页' , path:'/home' , img:require('../../static/images/background/home2.png') , Tab:require('../../static/images/background/home.png')},
                { title:'猪猪商城' , path:'/shop' , img:require('../../static/images/background/shop2.png') , Tab:require('../../static/images/background/shop.png')},
                { title:'庄园' ,path:'/game' , img:require('../../static/images/background/game2.png') , Tab:require('../../static/images/background/game.png')},
                { title:'交易中心' , path:'/deal' , img:require('../../static/images/background/deal2.png') , Tab:require('../../static/images/background/deal.png')},
                { title:'个人中心' , path:'/mine' , img:require('../../static/images/background/mine2.png') , Tab:require('../../static/images/background/mine.png')}
            ],
            showIndex:this.Index
       }
   },
   methods:{
        change(index){
            this.showIndex = index;
            let p = this.arr2[index].path;
            // console.log(this.showIndex)
            this.$router.push({path: p});
        }
   },
  components: {}
}
</script>

